import './style';
import { Component } from 'preact';
import { Router } from 'preact-router';
import Home from './home';
import Post from './post';
import { req } from './utils';

export default class App extends Component {
	state = {
		posts: [],
		author: ''
	}
	componentDidMount() {
		req(`https://api.github.com/repos/louismerlin/blissue/issues`).then(issues => {
			this.setState({
				posts: issues.filter(i => i.author_association === 'OWNER').map(i => ({
					id: i.id,
					body: i.body,
					title: i.title
				})),
				author: issues.find(i => i.author_association === 'OWNER').user.login
			});
		});
	}

	render(_, { posts, author }) {
		return (
			<div>
				<h1>Welcome to the blog of {author} !</h1>
				<Router>
					<Home path="/" posts={posts}/>
					<Post path="/:post" posts={posts}/>
				</Router>
			</div>
		);
	}
}



